@use "base/variables" as v;
@use "base/mixins" as m;

.boton{
  @include m.boton(v.$azul,v.$blanco);
  border-radius: 25px;
  &:hover{
    background-color: black;
  }
}
.boton-eliminar{
@include m.boton(v.$rojo,v.$blanco);
border-radius: 25px;
&:hover{
  background-color: black;
}
}